          border: 1px dashed #CAD6E2;
          height: 85px;
          width: 85px;
          text-align: center;
          position: relative;
          background: #fff;
          margin-bottom:16px;
          .content {
            position: absolute;
            top: 28px;
            left: 17px;
          }
        }
      }
      .avert-img {
        margin-bottom: 30px;
        .el-select {
          width: 420px;
        }
        .el-input {
          margin-top: 8px;
        }
      }
      .arrow-up {
        width: 0;
        height: 0;
        border-left: 15px solid transparent;
        border-right: 15px solid transparent;
        border-bottom: 15px solid #fff;
        position: absolute;
        margin-top: -30px;
        z-index: 99;
        left: 230px;
      }
      .logo-img {
        position: relative;
        width: 422px;
        height: 122px;
        background: url('../../../assets/img/mark.jpg');
        background-size: 8px 8px;
        overflow: hidden;
        img {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          height: 100px;
        }
        .hidden-btn {
          display: none;
          position: absolute;
          bottom: 0;
          height: 40px;
          line-height: 40px;
          text-align: right;
          width: 100%;
          background-color: rgba(80, 109, 149, 0.60);
          color: @white;
          .file {
            width: 100%;
            height: 40px;
            line-height: 40px;
            opacity: 0;
          }
          p {
            position: absolute;
            top: 0;
            right: 0;
            margin-right: 15px;
          }
        }
      }
      .desc {
        color: #7C98B6;
        margin-bottom: 30px;
        line-height: 21px;
        font-size:13px;
      }
      .hidden-container {
        p {
          height: 18px;
          line-height: 18px;
          font-size: 13px;
          /*margin-top: 16px;*/
          margin-bottom: 8px;
        }
        .p-address{
          margin-top: 16px;
        }
        .linkUrl {
          margin-bottom: 20px;
        }
      }
      .el-textarea {
        margin-bottom: 15px;
      }
      .el-button {
        padding: 9px 35px;
      }
      .products {
        border: 1px solid #DFE3EB;
        width: 420px;
        margin-bottom: 30px;
        .product-title {
          background: #F5F8FA;
          padding: 11px 16px;
          line-height: 22px;
          font-size: 13px;
          span {
            float: right;
          }
        }
        li {
          border-top: 1px solid #DFE3EB;
          padding: 5px 20px;
          overflow: hidden;
          position: relative;
          img {
            width: 42px;
            height: 42px;
          }
          span.p-name {
            display: inline-block;
            width: 200px;
            vertical-align: top;
            margin-left: 50px;
            top: 50%;
            transform: translateY(-50%);
            position: absolute;
            -webkit-box-orient: vertical;
            -ms-box-orient: vertical;
            -webkit-line-clamp: 3;
            -moz-line-clamp: 3;
            -ms-line-clamp: 3;
            max-height: 37px;
            overflow: hidden;
            display: -webkit-box;
          }
          .opt{
            float: right;
            line-height: 40px;
            display: none;
            .icon-font{
              color:#CBD6E3;
              font-size: 18px;
              cursor: pointer;
            }
          }
          &:hover{
            .opt{
              display: block;
            }
          }
        }
      }
    }
  }
</style>



